var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Por otra parte, la educación de los usuarios en prácticas seguras también contribuye a la seguridad de las redes inalámbricas. Los usuarios deben ser conscientes de los riesgos potenciales y tomar medidas preventivas, como no conectarse a redes públicas desconocidas, evitar compartir contraseñas y mantener sus dispositivos actualizados. La concienciación sobre los riesgos asociados con la conexión inalámbrica puede ayudar a reducir incidentes de seguridad y mejorar la protección de la red. ")]),_c('Separador'),_vm._m(4),_vm._m(5),_c('div',{staticClass:"mn p-5 bg-slyde mb-5"},[_c('div',{staticClass:"tarjeta bg-white p-5"},[_c('SlyderA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-9 mb-4 mb-md-0"},[_c('p',[_vm._v("WEP fue uno de los primeros protocolos implementados en redes Wi-Fi, diseñado para proporcionar un nivel básico de seguridad. Este protocolo emplea una clave de encriptación estática, que debía configurarse manualmente en cada dispositivo. Sin embargo, WEP mostró ser vulnerable a ataques debido a su encriptación débil y la reutilización de claves, lo que permite que los atacantes intercepten el tráfico y accedan a la red. Hoy en día, WEP es considerado obsoleto, pero su estudio es importante para comprender la evolución de los protocolos de seguridad en redes inalámbricas."),_c('br'),_c('br'),_vm._v("Para abordar las vulnerabilidades de WEP, se introdujo WPA como un estándar de seguridad mejorado. WPA utiliza el protocolo de integridad de clave temporal (TKIP), que genera una clave de encriptación nueva para cada paquete de datos. Este método mejora la seguridad, ya que no reutiliza las mismas claves y ofrece una protección más dinámica. Aunque WPA es más seguro que WEP, sigue teniendo ciertas vulnerabilidades que lo hacen susceptible a ataques avanzados. Por esta razón, WPA fue una solución temporal mientras se desarrollaba una alternativa más robusta: WPA2.")])]),_c('div',{staticClass:"col-lg-3 col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/45.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-9 mb-4 mb-md-0"},[_c('p',[_vm._v("WPA2 es actualmente el estándar de seguridad recomendado para redes Wi-Fi. Este protocolo introduce el cifrado AES (Advanced Encryption Standard), un método de encriptación mucho más seguro que el utilizado en WEP o WPA. AES utiliza claves de cifrado de 128 o 256 bits, haciendo que la información sea extremadamente difícil de descifrar sin la clave de acceso. Extra a a AES, WPA2 también implementa el protocolo de autenticación CCMP, que garantiza la integridad de los datos y protege contra ataques de red comunes, como el \"ataque de repetición\"."),_c('br'),_c('br'),_vm._v("Para configuraciones de seguridad más avanzadas, especialmente en redes corporativas, es común utilizar WPA2-Enterprise, una variante de WPA2 que permite autenticación en servidores RADIUS, en lugar de usar una clave compartida. Este método mejora la seguridad, ya que cada usuario tiene credenciales únicas y la red puede monitorear y gestionar cada conexión de forma individual. WPA2-Enterprise es ideal en entornos donde se necesita un control estricto sobre el acceso a la red, ya que proporciona autenticación y control de acceso a nivel de usuario.")])]),_c('div',{staticClass:"col-lg-3 col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/46.png"),"alt":"Texto que describa la imagen"}})])])])])],1)]),_vm._m(6),_vm._m(7),_c('Separador'),_vm._m(8),_vm._m(9),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-9 mb-lg-0 mb-3"},[_c('TabsA',{staticClass:"color-acento-botones mb-5"},[_c('div',{staticClass:"tarjeta p-4",staticStyle:{"background-color":"#efe8f7"},attrs:{"titulo":"Para redes empresariales"}},[_c('p',[_vm._v(" Se suele implementar la autenticación basada en RADIUS ("),_c('em',[_vm._v("Remote Authentication Dial-In User Service")]),_vm._v("), que utiliza un servidor de autenticación centralizado para gestionar el acceso de los usuarios. Este método permite que cada usuario tenga credenciales únicas, mejorando así el control sobre quién puede acceder a la red. El servidor RADIUS verifica las credenciales de cada usuario y, si son correctas, permite el acceso. Este tipo de autenticación es común en entornos empresariales y en redes públicas, donde se necesita un mayor control sobre el acceso y se requiere registrar el ingreso de cada usuario.")])]),_c('div',{staticClass:"tarjeta p-4",staticStyle:{"background-color":"#efe8f7"},attrs:{"titulo":"Extra a la autenticación"}},[_c('p',[_vm._v("El control de acceso permite dividir la red en diferentes niveles, separando, por ejemplo, las redes de invitados de las redes internas. Esto es posible mediante el uso de redes virtuales o VLANs (Virtual Local Area Networks), que permiten segmentar el tráfico y limitar el acceso a ciertas áreas de la red. Esta práctica es altamente relevante para minimizar el riesgo de que dispositivos no autorizados accedan a información confidencial o recursos críticos de la red. Adicionalmente, al segmentar la red, se facilita la gestión y el monitoreo del tráfico de datos.")])]),_c('div',{staticClass:"tarjeta p-4",staticStyle:{"background-color":"#efe8f7"},attrs:{"titulo":"En algunos casos"}},[_c('p',[_vm._v("Se utiliza la autenticación multifactor (MFA) como medida adicional de seguridad. La MFA requiere que el usuario proporcione más de una forma de verificación para acceder a la red, como una contraseña junto con un código de verificación temporal o una autenticación biométrica. Este enfoque aumenta significativamente la seguridad, ya que complica el acceso para usuarios no autorizados y reduce el riesgo de que alguien pueda obtener acceso únicamente mediante una contraseña robada o interceptada.")])]),_c('div',{staticClass:"tarjeta p-4",staticStyle:{"background-color":"#efe8f7"},attrs:{"titulo":"Finalmente"}},[_c('p',[_vm._v("Es importante implementar políticas de seguridad y capacitación para que los usuarios comprendan la importancia de la autenticación y el control de acceso. Los administradores de red deben asegurar que los usuarios no compartan sus credenciales y que actualicen sus contraseñas regularmente. La educación y concienciación sobre estas prácticas de seguridad fortalecen la protección de la red y ayudan a mantener un entorno de red seguro y confiable.")])])])],1),_vm._m(10)]),_c('Separador'),_vm._m(11),_vm._m(12),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-9 col-6 mb-lg-0 mb-3"},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--morada"}},[_c('div',{attrs:{"titulo":"Una de las principales funciones del monitoreo"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Es detectar y prevenir intrusiones. Esto es posible gracias a sistemas de detección de intrusos (IDS, Intrusion Detection System) y de prevención de intrusiones (IPS, Intrusion Prevention System), que son capaces de identificar comportamientos sospechosos en la red y responder automáticamente para mitigar las amenazas. Estos sistemas alertan al administrador cuando un dispositivo no autorizado intenta acceder a la red, lo que permite bloquear el acceso o limitar las acciones del dispositivo hasta que se investigue el incidente.")])]),_c('div',{attrs:{"titulo":"Extra al monitoreo"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La gestión de rendimiento es necesaria para mantener la calidad de la conexión y minimizar las interrupciones. La gestión incluye la optimización del ancho de banda y el control de acceso a la red para evitar la congestión en áreas de alta densidad de usuarios. A través de la configuración y ajuste de los Access Points y del análisis del tráfico de red, los administradores pueden asegurar que el ancho de banda se distribuya de forma eficiente y que cada usuario reciba una conexión estable. También es posible priorizar el tráfico de aplicaciones críticas, garantizando que no se vean afectadas por el uso de otros servicios.")])]),_c('div',{attrs:{"titulo":"El uso de herramientas de análisis de datos"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En redes inalámbricas ha permitido una evolución significativa en el monitoreo de redes. Estas herramientas generan informes detallados sobre el comportamiento de los dispositivos conectados, las horas de mayor uso y el tipo de tráfico que circula en la red. Esta información permite a los administradores identificar patrones de uso y anticipar problemas antes de que afecten a los usuarios. Por su parte, los análisis de datos pueden utilizarse para mejorar la seguridad, identificando comportamientos anómalos que podrían indicar intentos de ataque o el uso indebido de la red.")])]),_c('div',{attrs:{"titulo":"La actualización constante de software y firmware"}},[_c('p',{staticClass:"mb-0"},[_vm._v("De los dispositivos de red es otra práctica fundamental en la gestión. Las actualizaciones corrigen vulnerabilidades y mejoran el rendimiento de los Access Points, routers y otros dispositivos de la red. Un programa de actualización periódica asegura que la red esté protegida contra las amenazas más recientes y que funcione con los últimos estándares de seguridad.")])]),_c('div',{attrs:{"titulo":"Pruebas de instalación y funcionamiento"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Durante la instalación, se deben realizar pruebas parciales y continuas para asegurar que cada componente cumple con los estándares de calidad. Por ejemplo, al instalar cables de par trenzado o fibra óptica, se deben verificar parámetros de conexión y continuidad para asegurarse de que no haya errores en las conexiones. En la fase final, se realizan pruebas de rendimiento para confirmar que el sistema cumple con los requisitos de ancho de banda y calidad de señal.")])]),_c('div',{attrs:{"titulo":"Documentación y reportes de calidad"}},[_c('p',{staticClass:"mb-0"},[_vm._v("El control de calidad incluye la documentación detallada de cada paso del proceso de instalación, así como los resultados de las pruebas realizadas. Esta documentación es valiosa tanto para los clientes como para el equipo de mantenimiento, ya que proporciona un registro de las condiciones iniciales del sistema y permite realizar futuras inspecciones o ampliaciones con mayor facilidad. Los reportes de calidad documentan los parámetros de rendimiento alcanzados y validan que la instalación ha sido realizada conforme a los estándares.")])])])],1),_vm._m(13)]),_vm._m(14),_vm._m(15)],1)],1)
}
